import icons from 'url:../../img/icons.svg';

export default class View {
    _data;

    render(data) {
      if(!data || (Array.isArray(data) && data.length === 0)) 
      return this.renderError();  

      this._data = data;
      const markup = this._generateMarkup();
      this._clear();
      this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }

    update(data) {
      this._data = data;
      const newMarkup = this._generateMarkup();

      const newDOM = document.createRange().createContextualFragment(newMarkup);
      const newElements = Array.from(newDOM.querySelectorAll('*'));
      const currentElements = Array.from(this._parentElement.querySelectorAll('*'));

      newElements.forEach((newElement, i ) => {
        const curEl = currentElements[i];

        if(!newElement.isEqualNode(curEl) && newElement.firstChild?.nodeValue.trim() !== '') {
          curEl.textContent = newElement.textContent;
        }

        if(!newElement.isEqualNode(curEl)) {
          Array.from(newElement.attributes).forEach(attr => curEl.setAttribute(attr.name, attr.value))
        }
      });
    }
    
    _clear() {
        this._parentElement.innerHTML = '';
    }
  
    renderSpinner = function () {
      const markup = `
          <div class="spinner">
            <svg>
              <use href="${icons}#icon-loader"></use>
            </svg>
          </div>
          `;
  
      this._clear();
      this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }
  
    renderError(message = this._errorMessage) {
      const markup = `
        <div class="error">
          <div>
            <svg>
              <use href="${icons}#icon-alert-triangle"></use>
            </svg>
          </div>
          <p>${message}</p>
        </div>
        `;
  
        this._clear();
        this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }
  
    renderMessage(message = this._message) {
      const markup = `
        <div class="error">
          <div>
            <svg>
              <use href="${icons}#icon-alert-triangle"></use>
            </svg>
          </div>
          <p>${message}</p>
        </div>
        `;
  
        this._clear();
        this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }
}